import Vue from 'vue'
import Router from 'vue-router'
import team from './components/team/team'

Vue.use(Router)

export default new Router ({
  mode: 'history',
  routers: [
    {
      path: '/home',
      name: 'home',
      component: () => import('@/components/navbar/navbar')
    },
    {
      path: '/team',
      name: 'team',
      component: team
    },
    {
      path: '/service',
      name: 'service',
      component: () => import('@/components/body/card')
    },
    {
      path: '/contact',
      name: 'contact',
      component: () => import('@/components/contact/contact')
    }
  ]
})