<template>
<div>
  <div class="header">
    <a href="" class="logo">Muonn</a>
    <div class="menu-toggle"
      @click="activateMenuToggle"
      :class="{active: toggleActive}">
    </div>
    <nav :class="{active:toggleActive}">
      <company-register
        :dialog.sync='dialogCompany'
        :title="titleCompany">
      </company-register>
      <ul>
        <li><v-btn style="margin-top:15px" color="success" class="enterprise"
              @click="openDialog('company')" rounded>For Enterprise
            </v-btn>
        </li>
        <li><a @click="navigate('profile')">Profile</a></li>
        <li><a @click="navigate('service')">Service</a></li>
        <li><router-link to="/team">Team</router-link></li>
        <router-view></router-view>
        <li><v-btn style="margin:15px"
              color="primary"
              type="is-dark"
              @click="openDialog('login')" rounded>Log in
            </v-btn >
        </li>
        <li><v-btn style="margin:15px"
              color="primary"
              type="is-primary"
              @click="openDialog('register')" rounded>Join for free
            </v-btn>
        </li>
      </ul>
    </nav>
    <div class="clearfix"></div>
  </div>
  <wave-header></wave-header>
  <brief-profile class="service"></brief-profile>
  <card class="curriculum"></card>
  <bottom-footer></bottom-footer>
</div>
</template>

<script>
import card from '../body/card'
import briefProfile from '../body/briefProfile'
import wave2 from '../header/wave2'
import footer from '../footer/footer'
import companyRegister from '../dialog/manageRegisterEnterprise'


export default {
  name: 'navbar',
  components: {
    'wave-header': wave2,
    'card': card,
    'brief-profile': briefProfile,
    'bottom-footer': footer,
    'company-register': companyRegister
  },
  computed: {
    
  },
  data () {
    return {
      dialogLogin: false,
      dialogRegister: false,
      dialogCompany: false,
      toggleActive: false,
      titleCompany: 'Course for Company Level'
    }
  },
  methods: {
    activateMenuToggle () {
      if (this.toggleActive === false) {
        this.toggleActive = true
      } else {
        this.toggleActive = false
      }
    },
    openDialog (dialog) {
      if (dialog === 'login') {
        this.dialogLogin = true
      } else if (dialog === 'register') {
        this.dialogRegister = true
      } else {
        this.dialogCompany = true
      }
    },
    navigate (goTo) {
      if (goTo === 'profile') {
        this.$router.push({path: '/profile'})
      } else if (goTo === 'service') {
        this.$router.push({path: '/service'})
      } else {
        this.$router.push({path:'/team'})
      }
    }
  }
}
</script>

