<template>
  <v-layout justify-center>
    <v-dialog v-model="dialog" persistent max-width="900px">
      <v-card>
        <v-card-title>
          <span class="headline">{{title}}</span>
        </v-card-title>
        <v-card-text>
          <v-container grid-list-md>
            <v-layout wrap>
              <v-flex xs12>
                <v-text-field v-model="formCompany.name" label="Company Name*" required></v-text-field>
              </v-flex>
              <v-flex xs12>
                <v-text-field v-model="formCompany.address" label="Address*" required></v-text-field>
              </v-flex>
              <v-flex xs12>
                <v-text-field v-model="formCompany.email" label="Email*" required></v-text-field>
              </v-flex>
              <v-flex xs12>
                <v-text-field v-model="formCompany.phone" label="Phone Number" required></v-text-field>
              </v-flex>
              <v-flex xs6>
                <v-select
                  v-model="formCompany.course"
                  :items="courses"
                  item-text="state"
                  item-value="abbr"
                  label="Course"
                  persistent-hint
                  return-object
                  single-line
                ></v-select>
              </v-flex>
              <v-flex xs12>
                <v-textarea
                  counter
                  label="Message"
                  :rules="rulesMessage"
                  :value="formCompany.message"
                ></v-textarea>
              </v-flex>
            </v-layout>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="blue darken-1" text @click="close">Cancel</v-btn>
          <v-btn color="blue darken-1" text @click="save">Sign Up</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-layout>
</template>

<script>
  import { db } from '@/main'
  export default {
    props: {
      dialog: {
        type: Boolean,
        default: false
      },
      price: {
        type: Number,
        default: 0
      },
      title: {
        type: String,
        default: '',
      }
    },
    data () {
      return {
        rulesMessage: [v => v.length <= 600 || 'Max 600 characters'],
        formCompany: {
          name: '',
          email: '',
          address: '',
          phone: '',
          course: '',
          message: ''
        },
        courses: [
          'Python Dasar', 'Python Menengah', 'Python Tingkat Lanjut'
        ]
      }
    },
    methods: {
      save () {
        db.collection('company').add({
          company_name: this.formCompany.name,
          email: this.formCompany.email,
          address: this.formCompany.address,
          phone: this.formCompany.phone,
          course: this.formCompany.course,
          message: this.formCompany.message
        })
        this.$emit('update:dialog', false)
        let title = 'Pendaftaran ' + this.formCompany.course + ' ' + 'Sukses'
        let pesan =  "Pendaftaran Pelatihan tersebut akan segera di follow dalam waktu 1X24 Jam. Terima kasih" 
        this.$alert(pesan, title, {
          confirmButtonText: 'OK'})
        
        this.formCompany.name = ''
        this.formCompany.email = ''
        this.formCompany.address = ''
        this.formCompany.phone = ''
        this.formCompany.course = ''
        this.formCompany.message = ''
      },
      close () {
        this.$emit('update:dialog', false)
      }

    }
  }
</script>