<template>
  <v-layout justify-center>
    <v-dialog v-model="dialog" persistent max-width="600px">
      <v-card>
        <v-card-title>
          <span class="headline">{{title + ' ' + 'Course'}}</span>
        </v-card-title>
        <v-card-text>
          <v-container grid-list-md>
            <v-layout wrap>
              <v-flex xs12>
                <v-text-field v-model="formRegister.full_name" label="Full Name*" required></v-text-field>
              </v-flex>
              <v-flex xs12>
                <v-text-field v-model="formRegister.email" label="Email*" required></v-text-field>
              </v-flex>
              <v-flex xs12>
                <v-text-field v-model="formRegister.price" label="Price*" required></v-text-field>
              </v-flex>
              <v-flex xs12>
                <v-text-field v-model="formRegister.no_handphone" label="No handphone" required></v-text-field>
              </v-flex>
            </v-layout>
          </v-container>
          <small>*indicates required field</small>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="blue darken-1" text @click="close">Cancel</v-btn>
          <v-btn color="blue darken-1" text @click="save">Sign Up</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-layout>
</template>

<script>
  import { db } from '@/main'
  export default {
    props: {
      dialog: {
        type: Boolean,
        default: false
      },
      price: {
        type: Number
      },
      title: {
        type: String,
      }
    },
    data () {
      return {
        formRegister: {
          full_name: '',
          email: '',
          price: this.price,
          no_handphone: ''
        },
        cost: this.price + 123
      }
    },
    methods: {
      save () {
        let message = 'this is your payment. please pay with using 3 unique number to indentify you'
        db.collection('user').add({
          full_name: this.formRegister.full_name,
          email: this.formRegister.email,
          price: this.formRegister.price,
          no_handphone: this.formRegister.no_handphone
        })
        this.$emit('update:dialog', false)
        this.$alert(message + ' ' + this.cost, 'Payment', {
          confirmButtonText: 'OK',
          callback: ()  => {
            this.$message({
              type: 'info',
              message: `you register successfully to follow` + ' ' + this.title
            });
          }
        });
        
        this.formRegister.full_name = ''
        this.formRegister.email = ''
        this.formRegister.price = ''
        this.formRegister.phone_number = ''
      },
      close () {
        this.$emit('update:dialog', false)
      }

    }
  }
</script>